module.exports = "/*\r\n\r\nNavigation\r\n\r\n*/\n/* ---------- Component */\n:host,\n.navigation {\n  display: block;\n  border-bottom: 1px solid #CCCCCC; }\n/* ---------- Elements */\n.navigation__controls {\n  max-width: 1280px;\n  margin: 0 auto;\n  padding: 0 12px;\n  display: flex; }\n.navigation__controls > * {\n    flex: 1; }\n.navigation__control {\n  height: 48px;\n  line-height: 48px;\n  padding: 0 12px;\n  border-left: 1px solid #CCCCCC;\n  border-right: 1px solid #CCCCCC; }\n.navigation__control + .navigation__control {\n    margin-left: -1px; }\n.navigation__control:hover {\n    background-color: rgba(0, 0, 0, 0.1); }\n.navigation__control.is-active {\n    background-color: rgba(0, 0, 0, 0.1); }\n.navigation__control, .navigation__control:hover {\n    transition: background 0.15s; }\n.navigation__control__long-name {\n  display: none; }\n@media screen and (min-width: 768px) {\n    .navigation__control__long-name {\n      display: inline; } }\n@media screen and (min-width: 768px) {\n  .navigation__control__short-name {\n    display: none; } }\n"